import {
  Button,
  Collapse,
  DatePicker,
  Form,
  Input,
  message,
  Radio,
  Select,
} from 'antd'
import { useState } from 'react'
import { useHistory } from 'react-router-dom'

import { createProject } from '@/api/project'

export default function NewProject() {
  const [creating, setCreating] = useState(false)
  const history = useHistory()

  async function createNewProject(data) {
    try {
      setCreating(true)
      await createProject(data)
      message.success('')
      history.push('/project-grid')
    } catch {
      setCreating(false)
    }
  }

  const onFinish = values => {
    console.log('Success:', values)
    createNewProject(values)
  }

  const wrapperCol = { md: 18, lg: 10 }

  return (
    <>
      <h2 className="mb-4 text-xl font-bold"></h2>
      <Form
        className="p-6 bg-white rounded-lg"
        colon={false}
        initialValues={{ type: 'public' }}
        labelCol={{ span: 2 }}
        name="newProject"
        requiredMark={false}
        size="large"
        wrapperCol={wrapperCol}
        onFinish={onFinish}
      >
        <Form.Item
          label=""
          name="workplace"
          rules={[{ required: true, message: '' }]}
        >
          <Select placeholder="">
            {[
              { label: 'Magic', value: 'magic' },
              { label: 'LoveShare', value: 'love_share' },
            ].map(({ value, label }) => (
              <Select.Option key={value} value={value}>
                {label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>

        <Form.Item
          label=""
          name="name"
          rules={[{ required: true, message: '' }]}
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item
          label=""
          name="description"
          rules={[{ required: true, message: '' }]}
        >
          <Input.TextArea placeholder="" rows={4} />
        </Form.Item>

        <Form.Item
          label=""
          name="date"
          rules={[{ required: true, message: '' }]}
        >
          <DatePicker.RangePicker size="large" />
        </Form.Item>

        <Form.Item wrapperCol={{ ...wrapperCol, offset: 2 }}>
          <Button htmlType="submit" loading={creating} type="primary">
            
          </Button>
        </Form.Item>

        <Collapse ghost>
          <Collapse.Panel key="id" className="primary" header="">
            <Form.Item label="" name="aa">
              <Select placeholder="">
                <Select.Option value="jack">Jack</Select.Option>
              </Select>
            </Form.Item>
            <Form.Item label="" name="type" wrapperCol={wrapperCol}>
              <Radio.Group
                options={[
                  { label: '', value: 'public' },
                  { label: '', value: 'private' },
                ]}
              />
            </Form.Item>
          </Collapse.Panel>
        </Collapse>
      </Form>
    </>
  )
}
